import Vue from 'vue'
import Component from './func-indicator'   //  Indicator 

const IndicatorConstructor = Vue.extend(Component)  //  indicator

let instance
let haveInstance = false  //  indicator
const show = (opts, style) => {    //  indicator
  // console.log(Vue.prototype.$isServer)  
  if (Vue.prototype.$isServer || haveInstance) return  // dom
  // opts   this.$indic.show() 
  let num
  if ( !opts ) {
    num = { }
  } else if ( typeof opts === 'string' ) {
    num = { text: opts }
  } else if ( opts.constructor === Object ) {
    if ( !opts.autoClose ) {
      num = opts
    } else {
      num = opts
      num.isTimer = true
    }
  }
  // style  this.$indic.show() 
  let indicSty
  if (!style) {
    indicSty = {}
  } else {
    indicSty = style
  }
  // indicator Indicatorvue
  instance = new IndicatorConstructor({
    propsData: {    // opts indicator   props
      ...num
    },
    data () {
      return {
        indicSty
      }
    }
  })
  // Vue vm
  instance.vm = instance.$mount()
  document.body.appendChild(instance.vm.$el)  //dom
  haveInstance = true
  instance.visible = true    // visible true transition  @after-enter 
  instance.vm.$on('closed', () => {   //closed
    haveInstance = false
    document.body.removeChild(instance.vm.$el)  //  indicator
    instance.vm.$destroy()   // 
  })
  return instance
}

const  hidden = () => {
  if (!instance) return
  instance.visible = false
}

export default { show, hidden }   //   indicator


