import Indicator from './Indicator.vue'   //

export default {  //  Indicator ,    Indicator
  extends: Indicator,
  mounted () {  //  
    if (this.isTimer) {
      this.createTimer()
    }
  },
  data () {
    return {
      visible: false,
      indicSty: {}
    }
  },
  computed: {
    style () {
      return this.indicSty
    }
  },
  methods: { 
    createTimer () {   //   Indicator
      if (this.autoClose) {
        this.timer = setTimeout(() => {
          this.visible = false
        }, this.autoClose)
      }
    },
    clearTimer () {   // 
      if (this.timer) {
        clearTimeout(this.timer)
      }
    },
  },
  beforeDestory () {   //  
    this.clearTimer()
  }
}


